.navbar{
    width: 100vw;
    height: 3.7vw;
    display: flex;
    align-items: center;
}
.navbar span{
    font-weight: bold;
    font-size: 2vw;
    margin-left: 1vw;
    font-family: 'Dancing Script', cursive;
}
.links{
    margin-left: 2.3vw;
    display: flex;
    align-items: center;
    /* padding-left: 2.1vw; */
}

.links a{
    padding-left: 2.1vw;
    text-decoration: none;
    font-size: 1.2vw;
}

.navbar .mode{
    font-size: 0.9vw;
    font-family: Arial, Helvetica, sans-serif;
}


