.toggle{
    outline: none;
    appearance: none;
    width: 3.3vw;
    border: 0.1vw solid black;
    height: 1.65vw;
    margin-left: 47vw;
    border-radius: 0.9vw;
    position: relative;
    background: url(https://i.postimg.cc/9fNK6B69/download.png);
    background-size: cover;
    cursor: pointer;
    transition: all 0.8s;
    /* color: ; */
}

.toggle::before{
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: 50%;
    height: 100%;
    border-radius: 50%;
    background-color: orange;
    transform: translateY(-50%);
}

.toggle:checked{
    background: url(https://i.postimg.cc/Hn0nstVK/Screenshot-2020-04-16-at-1-07-19-PM.png);
    background-size: cover;
}

.toggle:checked:before{
    transform: translate(100%, -50%);
    background-color: rgb(182, 179, 179);
}

