.container{
    margin-top: 4.4vw;
    transform: translateX(24vw);
}
label{
    display: block;
    margin-bottom: 1.4vw;
    font-size: 1.75vw;
    letter-spacing: 0.04vw;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

textarea{padding: 0.3vw;}

.buttons{
    margin-top: 0.4vw;
}
.buttons button{
    margin-right: 1vw;
    padding: 0.47vw 0.8vw 0.47vw 0.8vw;
    border-radius: 0.4vw;
    border: 0;
    background-color: teal;
    color: white; 
}