@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  width: 100vw;
  overflow-x: hidden;
}


.navbar{
    width: 100vw;
    height: 3.7vw;
    display: flex;
    align-items: center;
}
.navbar span{
    font-weight: bold;
    font-size: 2vw;
    margin-left: 1vw;
    font-family: 'Dancing Script', cursive;
}
.links{
    margin-left: 2.3vw;
    display: flex;
    align-items: center;
    /* padding-left: 2.1vw; */
}

.links a{
    padding-left: 2.1vw;
    text-decoration: none;
    font-size: 1.2vw;
}

.navbar .mode{
    font-size: 0.9vw;
    font-family: Arial, Helvetica, sans-serif;
}



.toggle{
    outline: none;
    -webkit-appearance: none;
            appearance: none;
    width: 3.3vw;
    border: 0.1vw solid black;
    height: 1.65vw;
    margin-left: 47vw;
    border-radius: 0.9vw;
    position: relative;
    background: url(https://i.postimg.cc/9fNK6B69/download.png);
    background-size: cover;
    cursor: pointer;
    transition: all 0.8s;
    /* color: ; */
}

.toggle::before{
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: 50%;
    height: 100%;
    border-radius: 50%;
    background-color: orange;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

.toggle:checked{
    background: url(https://i.postimg.cc/Hn0nstVK/Screenshot-2020-04-16-at-1-07-19-PM.png);
    background-size: cover;
}

.toggle:checked:before{
    -webkit-transform: translate(100%, -50%);
            transform: translate(100%, -50%);
    background-color: rgb(182, 179, 179);
}


.container{
    margin-top: 4.4vw;
    -webkit-transform: translateX(24vw);
            transform: translateX(24vw);
}
label{
    display: block;
    margin-bottom: 1.4vw;
    font-size: 1.75vw;
    letter-spacing: 0.04vw;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

textarea{padding: 0.3vw;}

.buttons{
    margin-top: 0.4vw;
}
.buttons button{
    margin-right: 1vw;
    padding: 0.47vw 0.8vw 0.47vw 0.8vw;
    border-radius: 0.4vw;
    border: 0;
    background-color: teal;
    color: white; 
}
.reveal{ display: none; }

.block{
    /* border: ; */
    width: 20vw;
    height: 8vw;
    display: flex;
    flex-flow: column;
    justify-content: space-evenly;
    margin-top: 1.1vw;
}
.block span{  padding-left: 0.5vw;}


